import React from 'react';
import './tweetlist.css'

export class TweetList extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      query: "",
      data: [],
      filteredData: []
    };
    
  }



  componentDidMount(){
    fetch('http://localhost:4000/search-tweets').
    then((Response)=>Response.json()).
    then((findresponse,err)=>
    {      
      this.state.isFetching = false;
      this.setState(prevState =>({
        data: [...prevState.data, findresponse]
      }))
      console.log('hello: ', this.state.data[0]);
      
    })


  }




  render() {
    return (
      <div className="container_custom">
      hiiii

      </div>
      );
    }
  }



