import React from 'react';
import { Redirect } from 'react-router';
import { Player } from 'video-react';
import './video.css'

import './home.css'


export default class Home extends React.Component {

	constructor(props) {
		super(props);
		this.state = {data:[],isFetching:true,tweet:[]};
	}

	componentDidMount(){
		fetch('http://localhost:4000/twitter-trends').
		then((Response)=>Response.json()).
		then((findresponse,err)=>
		{      
			this.state.isFetching = false;
			this.setState(prevState =>({
				data: [...prevState.data, findresponse]
			}))
			console.log('hello: ', this.state.data[0].trends);
			
		})

		fetch('http://localhost:4000/twitter-tweets').
		then((Response)=>Response.json()).
		then((findresponse,err)=>
		{      
			this.state.isFetching = false;
			this.setState(prevState =>({
				tweet: [...prevState.tweet, findresponse]
			}))
			console.log('hello: ', this.state.tweet[0]);
		})
	}

	render() {
		console.log('render called')
		const {isFetching} = this.state;
		console.log(isFetching);
		let displaydata = 'loading';
		let displaydate = 'loading';
		
		
		if (!isFetching) displaydata = this.state.data[0].trends.map(el => <div>
			
			
			<a className="mdc-list-item trends-color" href="#" aria-current="page">
			<span className="mdc-list-item__text mdc-ripple-surface--primary" onClick={this.state.data[0].trends.url}>{el.name}</span>
			</a>

			</div>
			)

			if (!isFetching) displaydate = this.state.tweet.map(ell => <div>


				<div className="mdc-layout-grid__inner">
				<div className="mdc-layout-grid__cell--span-12" style={{margin:108}}>
				<div className="mg">
				<div className="tweets">
				<div className="mdc-list-item__text mdc-ripple-surface--primary"><img src={ell[0].user.profile_image_url} /></div>
				<div className="tweet-space">
				<strong>{ell[0].user.name}</strong>
				<p className="screen">@ {ell[0].user.screen_name}</p>
				<p>{ell[0].text}</p>

				</div>
				</div>
				<div className="tweets">
				<div className="mdc-list-item__text mdc-ripple-surface--primary"><img src={ell[1].user.profile_image_url} /></div>
				<div className="tweet-space">
				<strong>{ell[1].user.name}</strong>
				<p className="screen">@ {ell[1].user.screen_name}</p>
				<p>{ell[1].text}</p>

				</div>
				</div>
				<div className="tweets">
				<div className="mdc-list-item__text mdc-ripple-surface--primary"><img src={ell[2].user.profile_image_url} /></div>
				<div className="tweet-space">
				<strong>{ell[2].user.name}</strong>
				<p className="screen">@ {ell[2].user.screen_name}</p>
				<p>{ell[2].text}</p>
				</div>
				</div>
				<div className="tweets">
				<div className="mdc-list-item__text mdc-ripple-surface--primary"><img src={ell[3].user.profile_image_url} /></div>
				<div className="tweet-space">
				<strong>{ell[3].user.name}</strong>
				<p className="screen">@ {ell[3].user.screen_name}</p>
				<p>{ell[3].text}</p>
				</div>
				</div>
				<div className="tweets">
				<div className="mdc-list-item__text mdc-ripple-surface--primary"><img src={ell[4].user.profile_image_url} /></div>
				<div className="tweet-space">
				<strong>{ell[4].user.name}</strong>
				<p className="screen">@ {ell[4].user.screen_name}</p>
				<p>{ell[4].text}</p>
				</div>
				</div>
				</div>
				</div>
				</div>
				</div>

				)
				return (<div>

					
					<div className="container_custom">
					

					<aside className="mdc-drawer drawer1">
					<div className="mdc-drawer__content">
					<div className="profile_section"> 
					<div className="div_blue">
					</div>
					<div className="mdc-layout-grid__inner">
					<div className="mdc-layout-grid__cell--span-3">
					<div className="profile_img">
					<img src={require('./d.jpg')} />
					</div>
					</div>
					<div className="mdc-layout-grid__cell--span-1">
					</div>
					<div className="mdc-layout-grid__cell--span-8">
					<div className="profile_name">
					<h3>Dixit</h3>
					<p>@Dixit39991836</p>
					</div>
					</div>
					</div>
					<div className="mdc-layout-grid__inner">
					<div className="mdc-layout-grid__cell--span-4">
					<h5>Tweets</h5>
					<p>0</p>
					</div>
					<div className="mdc-layout-grid__cell--span-4">
					<h5>Following</h5>
					<p>0</p>
					</div>
					</div>
					</div>
					<h3 className="hashtag">Trends for you</h3>
					<nav className="mdc-list">
					<div>{displaydata}</div>
					</nav>
					</div>
					</aside>
					</div>

					<div>{displaydate}</div>


					</div>)
			}
		}




