import React, { Component } from 'react';
import { BrowserRouter as Router, Link, Route } from "react-router-dom";
import SignUp from './signup/signup.js';
import Home from './home/home.js';
import TweetList from './tweetlist/TweetList.js';
import './App.css';

class App extends React.Component {
	render() {
		return (
			<Router>
			<div className="navigation_bar">
			<div className="container_custom">
			<div className="link_left">
			<h3 className="twitt">Twitter Feed</h3>
			</div>
			<div className="link_right">
			
			<div className="top">
			<Link to="/home">
			<button className="mdc-button mdc-button--outlined login_btn" id="login_btn">
			<span className="mdc-button__label">Home</span>
			</button>
			</Link>
			<Link to="/signup">
			<button className="mdc-button mdc-button--outlined signup_btn" id="signup_btn">
			<span className="mdc-button__label">Signup</span>
			</button>
			</Link>
			<Link to="/tweetlist">
			<button className="mdc-button mdc-button--outlined add_tweet" id="add_twitter_button">
			<span className="mdc-button__label">Tweet</span>
			</button>
			</Link>
			</div>
			</div>
			</div>

			</div>
			<Route  path="/signup" component={SignUp} />
			<Route  path="/home" component={Home} />
			<Route path="/tweetlist" component={TweetList} />

			</Router>
			);
	}
}

export default App;



